<script>
import moment from "moment";
import {
  getRecentlyHotList,
  getHotBookList,
  getRateBookList,
  getGuessList,
  getActualList,
  detail
} from "@/api/MovieRest";
import {collect} from "@/api/user";
export default {
  data() {
    return {
      dialog:[],
      dialogVisible: true,
      HotData: [],
      RecentlyHotBook: [], //NewMovie
      RateMore: [],
      Guess:[],
      Actual:[],
      loading: true,
      loadingRH: true,//loadingN
      loadingRT: true,
      loadingRate: true,
      isSpinHot: false,
      isSpinRH: false,
      isSpinRate: false,
      login:false,
      list1:[],
      list2:[]
    };
  },
  filters: {
    fromNow(date) {
      return moment(date).fromNow();
    },
  },
  mounted(){
    this.getHot()
    this.getNew()
    this.getList()
    this.Judge()
  },
  methods: {
    Close(){
      this.dialogVisible = true;
    },
    showDialog(book){
      window.scroll(0,0)
      this.dialogVisible = false;
      detail({bid: book}).then((res) => {
        this.dialog = res[0].book;
        this.list1 = res[1].recs1;
        this.list2 = res[2].recs2;
      })
    },
    Judge(){
      if (window.sessionStorage.getItem('username')){
        this.login = true
        this.getActual();
        this.getGuess();
      }else{
        this.login = false
      }
    },
    Collection(book) {
      if(window.sessionStorage.getItem("username")){
        collect({username: window.sessionStorage.getItem('username'), bid: book}).then((res) => {
          if (res) {
            this.$notify({
              title: '收藏成功',
              message: ("请到个人页面查看")
            });
          }
        })
      }else{
        const h = this.$createElement;
        this.$notify({
          title: '请先登陆',
          message: h('i', { style: 'color: teal'}, '')
        });
      }
    },
    getGuess() {
      getGuessList({ username: window.sessionStorage.getItem("username") }).then((res) => {
        console.log(res)
        this.loading = false;
        this.Guess = res;
        if (this.isSpinHot) this.isSpinHot = !this.isSpinHot;
      });
    },
    getActual() {
      getActualList({ username: window.sessionStorage.getItem("username") }).then((res) => {
        this.loading = false;
        this.Actual = res;
        if (this.isSpinHot) this.isSpinHot = !this.isSpinHot;
      });
    },
    getHot() {
      getHotBookList({ num: 4 }).then((res) => {
        this.loading = false;
        this.HotData = res;
        if (this.isSpinHot) this.isSpinHot = !this.isSpinHot;
      });
    },
    getNew() {
      getRecentlyHotList({ num: 4 }).then((res) => {
        this.loadingRH = false;
        this.RecentlyHotBook = res;
        if (this.isSpinRH) this.isSpinRH = !this.isSpinRH;
      });
    },

    getList() {
      getRateBookList({ num: 4 }).then((res) => {
        this.loadingRate = false;
        this.RateMore = res;
        if (this.isSpinRate) this.isSpinRate = !this.isSpinRate;
      });
    },
    getInfoBookmc(){
      this.$alert('书名：《明朝那些事儿》<br >作者：当年明月<br >出版社：中国海关出版社<br >图书简介' +
          '：《明朝那些事儿》讲述从1344年到1644年，明朝三百年间的历史。作品以史料为基础，以年代和具体人物为主线，' +
          '运用小说的笔法，对明朝十七帝和其他王公权贵和小人物的命运进行全景展示，尤其对官场政治、战争、帝王心术着墨最多。' +
          '作品也是一部明朝政治经济制度、人伦道德的演义。<br >作者简介：当年明月，青年历史学者，畅销书作家。' +
          '专业职称为副研究员，明史学会会员。是心灵历史的开创者，担任国内多家电视台文史类节目主讲人。' +
          '所著作品《明朝那些事儿》销量过五百万册，为三十年来最畅销之史学读本。<br >评分：9.1', '图书信息', {
        dangerouslyUseHTMLString: true,
        confirmButtonText: '关闭',
        callback: action => {
          this.$message({
            type: 'info',
            message: `action: ${ action }`
          });
        }
      });
    },
    getInfoBookst(){
      this.$alert('书名：《三体》<br >' +
          '作者：刘慈欣 <br >' +
          '出版社：重庆出版社 <br >' +
          '图书简介：文化大革命如火如荼进行的同时。军方探寻外星文明的绝秘计划“红岸工程”取得了突破性进展。但在按下发射键的那一刻，历经劫难的叶文洁没有意识到，她彻底改变了人类的命运。地球文明向宇宙发出的第一声啼鸣，以太阳为中心，以光速向宇宙深处飞驰……四光年外，“三体文明”正苦苦挣扎——三颗无规则运行的太阳主导下的百余次毁灭与重生逼迫他们逃离母星。而恰在此时。他们接收到了地球发来的信息。在运用超技术锁死地球人的基础科学之后。三体人庞大的宇宙舰队开始向地球进发……人类的末日悄然来临<br >' +
          '作者简介：刘慈欣，祖籍河南，长于山西，中国科普作家协会会员，山西省作家协会会员，高级工程师。自1999年处女作《鲸歌》问世以来，刘慈欣已发表短篇科幻小说三十余篇、出版长篇科幻小说六部，并创下连续八年荣获中国科幻最高奖“银河奖”的纪录。他的长篇作品《三体》开创《科幻世界》月刊连载原创作品的先例，成为2006年度最受关注、最畅销的科幻小说。\n' +
          '评分：8.8', '图书信息', {
        dangerouslyUseHTMLString: true,
        confirmButtonText: '关闭',
        callback: action => {
          this.$message({
            type: 'info',
            message: `action: ${ action }`
          });
        }
      });
    },
    getInfoBooksq(){
      this.$alert(
          ' 书名：《沙丘》<br >' +
          '作者：[美] 弗兰克·赫伯特<br >' +
          '出版社： 江苏凤凰文艺出版社<br >' +
          '弗兰克·赫伯特（Frank Herbert，1920-1986）美国科幻小说家，作家。1920年10月08日生于华盛顿州。赫伯特是美国极具影响力的科幻巨匠，是与阿西莫夫并肩的大师。他在科幻文学中的地位如同托尔金在奇幻文学中的地位一样，无人可以动摇。他更是首位普及“生态 学”和“系统思想”的科幻作家，他教会了科幻作家如何赋予科幻小说以思想。赫伯特一生共创作了二十三部长篇小说和五部短篇小说集，其中尤为成功的便是沙丘系列小说。该系列的前三部长篇《沙丘》《沙丘救世主》和《沙丘之子》构成的“伟大沙丘三部曲”曾被翻拍成电影并引起巨大轰动。<br >' +
          '评分：7.7', '图书信息', {
            dangerouslyUseHTMLString: true,
            confirmButtonText: '关闭',
            callback: action => {
              this.$message({
                type: 'info',
                message: `action: ${ action }`
              });
            }
          });
    },
    getInfoBookyh(){
      this.$alert('书名：《银河帝国1：基地》<br >' +
